/*
    订单页面接口
*/

import request from "@/units/request"


//获取订单列表
export function api_getOrder(params){
    return request.get("/order/search",params)
}

//获取首页数据
export function api_getHomeData(){
    return request.get("/order/totaldata")
}
//获取订单统计数据
export function api_getOrderData(params){
    return request.get("/order/ordertotal",params)
}