<template>
  <div>
    <el-form :inline="true" :model="orderForm" class="order-form">
      <!-- 订单号 -->
      <el-form-item label="订单号">
        <el-input v-model="orderForm.orderNo" placeholder="订单号"></el-input>
      </el-form-item>
      <!-- 收货人 -->
      <el-form-item label="收货人">
        <el-input v-model="orderForm.consignee" placeholder="收货人"></el-input>
      </el-form-item>
      <!-- 手机号 -->
      <el-form-item label="手机号">
        <el-input v-model="orderForm.phone" placeholder="手机号"></el-input>
      </el-form-item>
      <!--订单状态-->
      <el-form-item label="订单状态">
        <el-select v-model="orderForm.orderState" placeholder="订单状态">
          <el-option label="已送达" value="已送达"></el-option>
          <el-option label="未送达" value="未送达"></el-option>
          <el-option label="派送中" value="派送中"></el-option>
        </el-select>
      </el-form-item>
      <!-- 选择时间 -->
      <el-form-item label="选择时间">
        <el-col :span="11">
          <el-date-picker
          value-format="yyyy:MM:dd HH:mm:ss"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
             v-model="orderForm.date"
          >
            >
          </el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
    <div class="table">
      <el-table :data="orderData" border style="width: 80%">
        <el-table-column fixed prop="orderNo" label="订单号" width="150">
          <template slot-scope="scope">{{ scope.row.orderNo }}</template>
        </el-table-column>
        <el-table-column prop="orderTime" label="下单时间" width="160">
          <template slot-scope="scope">{{ scope.row.orderTime }}</template>
        </el-table-column>
        <el-table-column prop="phone" label="手机号" width="120">
          <template slot-scope="scope">{{ scope.row.phone }}</template>
        </el-table-column>
        <el-table-column prop="consignee" label="收货人" width="120">
          <template slot-scope="scope">{{ scope.row.consignee }}</template>
        </el-table-column>
        <el-table-column prop="deliverAddress" label="配送地址" width="120">
          <template slot-scope="scope">{{ scope.row.deliverAddress }}</template>
        </el-table-column>
        <el-table-column prop="deliveryTime" label="送达时间" width="160">
          <template slot-scope="scope">{{ scope.row.deliveryTime }}</template>
        </el-table-column>
        <el-table-column prop="remarks" label="用户备注" width="120">
          <template slot-scope="scope">{{
            scope.row.remarks
          }}</template> </el-table-column
        ><el-table-column prop="orderAmount" label="订单金额" width="120">
          <template slot-scope="scope">{{ scope.row.orderAmount }}</template>
        </el-table-column>
        <el-table-column prop="orderState" label="订单状态" width="120">
          <template slot-scope="scope">{{ scope.row.orderState }}</template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="check(scope.row)" type="text" size="small"
              >查看</el-button
            >
            <el-button type="text" size="small" @click="edit(scope.row)"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[1, 3, 5, 10]"
      :page-size="3"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { api_getOrder } from "@/api/order";
import moment from "moment";
export default {
  data() {
    return {
      //订单查询表单数据
      orderForm: {
        orderNo: "",
        consignee: "",
        phone: "",
        orderState: "",
        date: "",
      },
      total: 5, //总页数
      currentPage: 1, //当前页数
      pageSize: 3, //当前条数
      //表格数据
      orderData: [],
    };
  },
  methods: {
    //获取表格数据函数
    async getOrderList() {
      let { total, data } = await api_getOrder({
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        orderNo: this.orderForm.orderNo,
        consignee: this.orderForm.consignee,
        phone: this.orderForm.phone,
        orderState: this.orderForm.orderState,
        date: this.orderForm.date==null?JSON.stringify(""):JSON.stringify(this.orderForm.date)
      });
      //遍历时间，更改时间格式
      data.forEach((item) => {
        item.orderTime = moment(item.orderTime).format("YYYY:MM:DD HH:mm:ss");
        item.deliveryTime = moment(item.deliveryTime).format(
          "YYYY:MM:DD HH:mm:ss"
        );
      });
      this.orderData = data;
      this.total = total;
    },

    //条数变化
    handleSizeChange(val) {
      this.pageSize = val;
      this.getOrderList();
    },
    //当前页数变化
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getOrderList();
    },
    //表单查询按钮
    onSubmit() {
      this.currentPage = 1;
      this.getOrderList();
    },
    //表格查看
    check() {},
    //编辑按钮
    edit() {},
  },
  created() {
    this.getOrderList();
  },
};
</script>

<style lang="less" scoped>
</style>